<template>
  <v-container>
    <v-row>
      <v-col md="8" offset-md="2">
        <v-card class="pa-2" :disabled="submitting">
          <div v-if="loading">
            <v-card-text>
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
              <br />
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
              <br />
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
            </v-card-text>
          </div>
          <div v-else>
            <v-card-title> Hi {{ client }} </v-card-title>
            <v-card-text>
              Kindly provide the reason for the none-acceptance.
            </v-card-text>
            <v-form ref="form">
              <v-card-text>
                <v-textarea
                  ref="reason"
                  v-model="reason"
                  label="Reason*"
                  :rules="[
                    rules.required,
                    rules.minimum(2),
                    rules.maximum(5000),
                  ]"
                  outlined
                  dense
                  rows="4"
                ></v-textarea>
                <v-btn
                  color="primary"
                  class="text-capitalize"
                  @click="confirmationDialog = true"
                >
                  Submit
                </v-btn>
              </v-card-text>
            </v-form>
          </div>
        </v-card>
      </v-col>
    </v-row>

    <confirmation-message
      v-if="confirmationDialog"
      :confirmationData="confirmationData"
      @closeConfirmationDialog="closeConfirmationDialog"
      @onSubmit="onSubmit"
    />
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import formRules from "@/helpers/formRules";
import ConfirmationMessage from "@/components/ConfirmationMessage.vue";

export default {
  components: { ConfirmationMessage },

  data: () => ({
    rules: formRules,
    ticketId: null,
    client: null,
    reason: null,
    confirmationDialog: false,
    confirmationData: {
      title: "Are you sure you want to submit?",
      text: "This action cannot be undone.",
      closeBtnText: "Cancel",
      closeBtnColor: "gray",
      saveBtnText: "Submit",
      saveBtnColor: "primary",
    },
  }),

  created() {
    this.getRecord();
  },

  computed: {
    ...mapGetters("reason", ["loading", "ticket", "submitting"]),
  },

  methods: {
    ...mapActions("reason", ["showRecord", "storeReason"]),

    async getRecord() {
      await this.showRecord(this.$route.params.id);

      this.$refs.reason.focus();

      if (Object.keys(this.ticket).length) {
        this.ticketId = this.ticket.id;
        this.client = this.ticket.client;
      }
    },

    async onSubmit() {
      this.confirmationDialog = false;
      if (this.$refs.form.validate()) {
        const formData = new FormData();
        formData.append("ticketId", this.ticketId);
        formData.append("reason", this.reason);
        await this.storeReason(formData);
        this.$refs.form.reset();
      }
    },

    closeConfirmationDialog() {
      this.confirmationDialog = false;
    },
  },
};
</script>